.flat-links {
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
  
  .flat-link {
    text-decoration: none;
    color: inherit;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ccc;
  }
  
  .link-container {

    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .link-content {
    display: flex;
    align-items: center;
    width: 100%;
  }
  
  .icon {
    font-size: 1.2rem;
  }
  