.cards-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}

.card {
    background-color: var(--cardBackground);
    
    padding: 20px 20px;
    border-radius: 15px;
    text-align: left;
    margin: 10px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}

.card .title{
    color: var(--typography500);
    font-size: 1.2rem;
}


.isMb{
    display: flex;
}
.isLg{
    display: none;
}
@media (min-width: 768px) {
    .isMb{
        display: none;
    }
    .isLg{
        display: flex;
    }
    .cards-container {
        flex-direction: row;
        flex-wrap: nowrap;
    }

    .card {
        width: 30%;
    }
    select#mes-select {
        width: auto;
        display: flex;
        margin: auto;
        margin-top: 10px;
    }
}