.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: end;
    justify-content: center;
    z-index: 2;
}

.modal-content {
    width: 100%;
    padding: 20px;
    border-radius: 20px 20px 0 0;
    text-align: center;
    padding-bottom: 2rem;
}
.modal-content  label{
display: none;
}