/* Reset styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  
  font-family: 'Inter', sansSerif;
  transition: background-color 0.3s, color 0.3s;
}

.App {
  padding: 20px;
  text-align: center;
}



/* Button Styles */
.btn {
  border-radius: 8px;
  margin: 2px;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
}

/* Input Styles */
input,
select,
textarea {
  background-color: #ffffff;
  border: 1px solid #ced4da;
  color: #333333;
  padding: 20px;
  border-radius: 0.25rem;
  font-family: 'Inter', sansSerif;
}


.btn-add {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  position: fixed;
  padding: 0;
  right: 20px;
  bottom: 60px;
  border-radius: 30px;
  background-color: var(--primaryColor);

  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s, color 0.3s;
}



/* Estilo base para o modal */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease, visibility 0.2s ease;
}

.modal.show {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  animation: slideUp 0.2s ease-out;
}
.modal-content{
  max-width: 560px;
}
.modal-content.slide-up {
  
  animation: slideUp 0.2s ease-out forwards;
}

.modal-content.slide-down {
  animation: slideDown 0.2s ease-out forwards;
}

@keyframes slideUp {
  from {
      transform: translateY(50px);
      opacity: 1;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}

@keyframes slideDown {
  from {
      transform: translateY(0);
      opacity: 1;
  }
  to {
      transform: translateY(90%);
      opacity: 0;
  }
}



/*
* LOGIN / REGISTER
*/

.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: var(--backgroundColor);
}

.login-container {
  max-width: 400px;
  padding: 2rem;
  background-color: var(--cardBackground);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.login-container h2 {
  margin-bottom: 1rem;
  color: var(--textColor);
}

.login-container label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--textColor);
}

.login-container input[type="text"],.login-container input[type="email"],
.login-container input[type="password"] {
  font-size: 1rem;
  background-color: var(--inputBackground);
  border: 1px solid var(--inputBorder);
  color: var(--textColor);
  padding: 12px 16px;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
  margin: 8px 0 16px 0;
  outline: none;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.login-container input[type="text"]:focus,.login-container input[type="email"]:focus,
.login-container input[type="password"]:focus {
  border-color: var(--inputFocusBorder);
  box-shadow: 0 0 8px var(--inputFocusShadow);
}

.login-container button {
  width: 100%;
  padding: 12px 16px;
  font-size: 1rem;
  background-color: var(--primaryColor);
  color: var(--white100);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-container button:hover {
  background-color: var(--primaryColor);
  filter: brightness(95%); /* Ajuste o valor conforme necessário */
}

.login-container p {
  display: block;
  margin-bottom: 1rem;
  text-align: right;
  text-decoration: none;
  font-size: 0.875rem;
}
.login-container .forgot-password {
  display: block;
  margin-top: 1rem;
  text-align: right;
  color: var(--primaryColor);
  text-decoration: none;
  font-size: 0.875rem;
}

.login-container .forgot-password:hover {
  text-decoration: underline;
}


.login-container .error-message,.register-container .error-message {
  color: red;
  margin-bottom: 10px;
  display: block;
  text-align: center;
  background: #f3ecec;
  border-radius: 6px;
  padding: 6px;
}
.login-container a{
  color: var(--primaryColor);
  text-decoration: none;
  font-size: 0.875rem;
}
.login-container a:hover {
  text-decoration: underline;
}

/*
* BUTTONS FILTER
*/

.slider-container {
  display: flex;
  justify-content: center;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  padding: 10px 0;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.slider-container::before,
.slider-container::after {
  content: '';
  flex: 0 0 40px; /* Largura do espaço adicional */
}
.slider-container::-webkit-scrollbar {
  display: none;  /* Hide scrollbar for Chrome, Safari, and Opera */
}

.slider-button {
  flex: 0 0 auto;
  padding: 10px 20px;
  margin: 0 5px;
  border: none;
  border-radius: 20px;
  background-color: #ddd;
  color: #333;
  cursor: pointer;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.01);
  transition: background-color 0.3s, color 0.3s;
}

.slider-button.active {
  background-color: #007bff;
  color: #fff;
}

