.button-row {
    display: flex;
    justify-content: space-between;
}

button:hover {
    cursor: pointer;
}

.wrapper {

    border-radius: 1em;
}

.orange {
    background-color: #FF9500;
    color: white;
}

.gray {
    background-color: #505050;
    color: white;
}

.result {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0 10px;
    font-size: 3.5em;

}

.result {
    font-size: 3.5em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: right;
    box-sizing: border-box;
    position: relative;
  }
  .result::before{
    content: '';
    width: 20px;
    height: 100%;
    position: absolute;
    left: 0;
    background: var(--cardBackground);
    background: linear-gradient(90deg, var(--cardBackground) 0%, rgba(0, 0, 0, 0) 100%);
  }
  