

/* Light Theme Styles */

body {
    color: var(--textColor);
}

.App {
    padding: 20px;
    text-align: center;
}

.main,.content {
    border-radius: 20px;
    padding: 20px;
}

.main h2 {
    text-align: center;
}

.list-group-item {
    color: var(--textColor);
    background-color: var(--cardBackground);
    padding: 20px;
    margin: 10px 0;
    border-radius: 8px;
    /*box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);*/
    transition: background-color 0.3s, color 0.3s;
}

.list-group-item:hover {
    background-color: var(--lightColor);
}


.item-category span, .item-status span {
    background: var(--backgroundColor);
    padding: 4px 8px;
    border-radius: 8px;
}
/* Button Styles */
.btn {
    border-radius: 8px;
    margin: 2px;
    padding: 8px 20px;
    font-size: 1rem;
    font-weight: 500;
    border: none;
    cursor: pointer;
    transition: all ease-in-out 0.3s;
}

.btn.primary {
    background-color: var(--primaryColor);
}

.btn.secondary {
    background-color: var(--secondaryColor);
}

.btn.success {
    background-color: var(--successColor);
}

.btn.danger {
    background-color: var(--dangerColor);
}

.btn.info {
    background-color: var(--infoColor);
}

.btn:hover {
    opacity: 0.9;
}

/* Text Styles */
.text-primary {
    color: var(--primaryColor);
}

.text-secondary {
    color: var(--secondaryColor);
}

.text-success {
    color: var(--successColor);
}

.text-danger {
    color: var(--dangerColor);
}

.text-info {
    color: var(--infoColor);
}

/* Input Styles */
input,
select,
textarea {
    background-color: #ffffff;
    border: 1px solid #ced4da;
    color: #333333;
    padding: 0.375rem 0.75rem;
    border-radius: 0.25rem;
    font-family: 'Inter', sans-serif;
}

input:focus,
select:focus,
textarea:focus {
    border-color: #007bff;
    outline: 0;
   box-shadow: 0px 4px 8px 2px rgba(0, 123, 255, 0.25);
}



input[type="text"],
input[type="number"],
select,
input[type="date"] {

    font-size: 1rem;
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
    padding: 12px 16px;
    /* Padding interno para uma sensação de toque */
    border-radius: 8px;
    /* Bordas arredondadas */
    width: 100%;
    /* Largura total do contêiner pai */
    box-sizing: border-box;
    /* Inclui padding e border no cálculo da largura */
    /*margin: 8px 0 16px 0;*/
    /* Margem para separar os elementos */
    outline: none;
    /* Remove o outline padrão */
    transition: border-color 0.3s, box-shadow 0.3s;
    /* Transições suaves */
}

/* Estilo para focar no input */
input[type="text"]:focus,
input[type="number"]:focus,
select:focus,
input[type="date"]:focus {
    border-color: var(--inputFocusBorder);
    /* Cor da borda ao focar */
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow);
    /* Sombra ao focar */
}

select {
    -webkit-appearance: none;
    /* Remove a seta padrão no Chrome/Safari */
    -moz-appearance: none;
    /* Remove a seta padrão no Firefox */
    appearance: none;
    /* Remove a seta padrão no Edge */
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%27http://www.w3.org/2000/svg%27%20viewBox%3D%270%200%204%205%27%3E%3Cpath%20fill%3D%27333%27%20d%3D%27M2%200L0%202h4zm0%205L0%203h4z%27/%3E%3C/svg%3E');
    background-repeat: no-repeat;
    background-position: right 12px center;
    background-size: 8px 10px;
    padding-right: 32px;
    /* Espaço extra para a seta */
}

/* Modal Styles */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
   
    z-index: 2;
}



.modal-content {
    width: 80%;
    background: var(--modalBackground);
    padding: 20px;
    border-radius: 20px 20px 0 0;
    text-align: center;

    -webkit-box-shadow: 0px -7px 20px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px -7px 20px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px -7px 20px 0px rgba(0, 0, 0, 0.2);
}

.modal-content label {
    color: var(--textColor);
    display: block;
}

.modal-content input[type="text"],
.modal-content input[type="number"],
.modal-content select,
.modal-content input[type="date"] {
    font-size: 1rem;
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
    padding: 15px 0;
    border-radius: 4px;
    /*margin-bottom: 16px;*/
    margin: 8px 0px 10px 0px;
}

.modal-content input[type="text"]:focus,
.modal-content input[type="number"]:focus,
.modal-content select:focus,
.modal-content input[type="date"]:focus {
    border-color: var(--input-focusBorder);
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow);
}


body.light .modal-content button.btn-lg,body.light button.btn-add {
    color: var(--white100);
}

body.light .modal-content button.btn-lg{
    width: 100%;
    padding: 12px 16px;
    font-size: 1rem;
    color: var(--white100);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.modal-content button.btn-lg.primary {
    background-color: var(--primaryColor);
}

.modal-content button.btn-lg.secondary {
    background-color: var(--secondaryColor);
}

.btn:hover{
    filter: brightness(95%);
}
.modal-content button.btn-lg:hover {
    opacity: 0.9;
}


.modal-content input[type="text"],
.modal-content input[type="number"],
.modal-content select,
.modal-content input[type="date"] {

    font-size: 1rem;
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
    padding: 12px 16px;
    /* Padding interno para uma sensação de toque */
    border-radius: 8px;
    /* Bordas arredondadas */
    width: 100%;
    /* Largura total do contêiner pai */
    box-sizing: border-box;
    /* Inclui padding e border no cálculo da largura */
    margin: 8px 0 16px 0;
    /* Margem para separar os elementos */
    outline: none;
    /* Remove o outline padrão */
    transition: border-color 0.3s, box-shadow 0.3s;
    /* Transições suaves */
}

/* Estilo para focar no input */
.modal-content input[type="text"]:focus,
.modal-content input[type="number"]:focus,
.modal-content select:focus,
.modal-content input[type="date"]:focus {
    border-color: var(--inputFocusBorder);
    /* Cor da borda ao focar */
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow);
    /* Sombra ao focar */
}

/* Estilo para o input de data */
.modal-content input[type="date"] {
    min-width: 95%;
    font-size: 1rem;
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
    padding: 12px 16px;
    /* Padding interno para uma sensação de toque */
    border-radius: 8px;
    /* Bordas arredondadas */
    width: calc(100%) !important;
    /* Largura total do contêiner pai menos o espaço extra para o ícone */
    box-sizing: border-box;
    /* Inclui padding e border no cálculo da largura */
    margin: 8px 0 16px 0;
    /* Margem para separar os elementos */
    outline: none;
    /* Remove o outline padrão */
    transition: border-color 0.3s, box-shadow 0.3s;
    /* Transições suaves */



}

/* Estilo para focar no input de data */
.modal-content input[type="date"]:focus {
    border-color: var(--inputFocusBorder);
    /* Cor da borda ao focar */
    box-shadow: 0 0 8px var(--inputFocusShadow);
    /* Sombra ao focar */
}

/* Estilo para o select */
.modal-content select {
    -webkit-appearance: none;
    /* Remove a seta padrão no Chrome/Safari */
    -moz-appearance: none;
    /* Remove a seta padrão no Firefox */
    appearance: none;
    /* Remove a seta padrão no Edge */
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%27http%3A//www.w3.org/2000/svg%27%20viewBox%3D%270%200%204%205%27%3E%3Cpath%20fill%3D%27%23333%27%20d%3D%27M2%200L0%202h4zm0%205L0%203h4z%27/%3E%3C/svg%3E');
    /* Imagem de seta */
    background-repeat: no-repeat;
    background-position: right 12px center;
    background-size: 8px 10px;
    padding-right: 32px;
    /* Espaço extra para a seta */
}

/* Estilo para elementos de data no Firefox */
@-moz-document url-prefix() {
    .modal-content input[type="date"] {
        padding: 12px 16px;
    }
}


/* Estilos base para o componente */
.list-group {
    padding: 0;
    overflow: auto;
}

.list-group-item {
    border: none;
    margin-bottom: 10px;
}

.item-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.item-info {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.group-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.item-name {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.item-category,
.item-date,
.item-status {
    margin-top: 10px;
}

.item-actions {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-top: 10px;
}
@media (max-width: 768px) {
    .item-name,
    .item-value,
    .item-category,
    .item-date,
    .item-status {
        min-width: 60px;
    }
}
/* Estilos para telas maiores que 768px */
@media (min-width: 768px) {
    .item-container {
        flex-direction: row;
        align-items: center;
    }

    .item-info {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: auto;
    }

    .item-name,
    .item-value,
    .item-category,
    .item-date,
    .item-status {
        min-width: 60px;
        margin-top: 0;
        margin-right: 20px;
    }

    .item-actions {
        margin-top: 0;
    }
}

/* Estilos para telas maiores que 960px */
@media (min-width: 960px) {

    .item-name,
    .item-value,
    .item-category,
    .item-date,
    .item-status {
        min-width: 150px;
        margin-top: 0;
        margin-right: 20px;
    }

    .item-name {
        min-width: 180px;
    }
}

/* Estilos para telas maiores que 1200px */
@media (min-width: 1200px) {

    .item-name,
    .item-value,
    .item-category,
    .item-date,
    .item-status {
        min-width: 220px;
        margin-top: 0;
        margin-right: 20px;
    }

}