/* SkeletonLoader.css */
.skeleton-loader {
    min-width: 10px;
    width: 100%;
    height: 16px;
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    background-color: var(--gray100);
}

.animated-background {
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(270deg, var(--gray100), var(--gray200), var(--gray100));
    background-size: 400% 400%;
    animation: gradientAnimation 2s ease infinite;
    border-radius: 4px;
}

@keyframes gradientAnimation {
    0%,100% {
        background-position: 100% 50%;
    }
    50% {
        background-position: 0 50%;
    }
}
