/* Dark Theme Styles for specific elements */

body.dark {
    color: var(--textColor);
}

body.dark .list-group-item {
    background-color: var(--listBackground);
    color: var(--textColor);
}

body.dark .list-group-item:hover {
    background-color: #333333; /* Um tom mais escuro para hover */
}

body.dark .item-category span,
body.dark .item-status span {
    background: var(--backgroundColor); /* Cor de fundo para categorias e status */
}

/* Button Styles */

body.dark .btn.primary {
    background-color: var(--primaryColor);
}

body.dark .btn.secondary {
    background-color: var(--secondaryColor);
}

body.dark .btn.success {
    background-color: var(--successColor);
}

body.dark .btn.danger {
    background-color: var(--dangerColor);
}

body.dark .btn.info {
    background-color: var(--infoColor);
}

/* Text Styles */
body.dark .text-primary {
    color: var(--primaryColor);
}

body.dark .text-secondary {
    color: var(--secondaryColor);
}

body.dark .text-success {
    color: var(--successColor);
}

body.dark .text-danger {
    color: var(--dangerColor);
}

body.dark .text-info {
    color: var(--infoColor);
}

/* Input Styles */
body.dark input,
body.dark select,
body.dark textarea {
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
}

body.dark input:focus,
body.dark select:focus,
body.dark textarea:focus {
    border-color: var(--inputFocusBorder);
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow);
}

body.dark select {
    /* Exemplo de imagem de fundo para o select */
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%27http://www.w3.org/2000/svg%27%20viewBox%3D%270%200%204%205%27%3E%3Cpath%20fill%3D%27fff%27%20d%3D%27M2%200L0%202h4zm0%205L0%203h4z%27/%3E%3C/svg%3E');
}

/* Modal Styles */
body.dark .modal {
    background: rgba(0, 0, 0, 0.1); /* Fundo semitransparente para o modal */
}

body.dark .modal-content {
    background: var(--modalBackground);
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow); /* Sombra mais forte para o modal */
}

body.dark .modal-content label {
    color: var(--textColor);
}

body.dark .modal-content input[type="text"],
body.dark .modal-content input[type="number"],
body.dark .modal-content input[type="date"] {
    background-color: var(--inputBackground);
    border: 1px solid var(--inputBorder);
    color: var(--textColor);
}

body.dark .modal-content input[type="text"]:focus,
body.dark .modal-content input[type="number"]:focus,
body.dark .modal-content input[type="date"]:focus {
    border-color: var(--inputFocusBorder);
   box-shadow: 0px 4px 8px 2px var(--inputFocusShadow);
}

body.dark .modal-content button.btn-lg.primary {
    background-color: var(--primaryColor);
    color: var(--textColor);
}

body.dark .modal-content button.btn-lg.secondary {
    background-color: var(--secondaryColor);
    color: var(--textColor);
}