.profile-container {
    
}

.profile-header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-avatar {
    margin-bottom: 20px;
    border-radius: 50%;
    background: var(--cardBackground);
}

.avatar-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.avatar-placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    font-size: 2rem;
}

.profile-display-name {
    color: var(--infoColor);
    margin-bottom: 5px;
}

.profile-type {
    color: var(--gray400);
    font-size: 0.8rem;
}

.profile-form {
    margin-top: 20px;
}

.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    color: var(--typography500);
}

input[type="password"] {
    width: 100%;
    padding: 10px;
    border: 1px solid var(--inputBorder);
    border-radius: 5px;
    background-color: var(--inputBackground);
    font-size: 1rem;
    transition: border-color 0.3s ease;
}

input[type="password"]:focus {
    outline: none;
    border-color: var(--primaryColor);
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

.error-message {
    color: var(--dangerColor);
    font-size: 0.9rem;
    margin-top: 5px;
}

.success-message {
    color: var(--successColor);
    font-size: 0.9rem;
    margin-top: 5px;
}

.profile-actions {
    margin-top: 20px;
    text-align: center;
}

.btn {
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.btn.primary {
    background-color: var(--primaryColor);
    color: var(--white100);
}

.btn.danger {
    background-color: var(--dangerColor);
    color: var(--white100);
}
